<template>
  <div>
    <div class="switch-field">
      <input type="radio" id="major" name="scale-type" value="major" v-model="scaleType" checked />
      <label for="major">Major</label>
      <input type="radio" id="minor" name="scale-type" value="minor" v-model="scaleType" />
      <label for="minor">Minor</label>
    </div>
    <div class="form-group">
      <input type="checkbox" id="finger" v-model="scaleFinger" />
      <label for="finger">Finger</label>
    </div>
    <Delimiter class="hr-5" />
    <transition name="fade">
      <MajorScale v-if="scaleType=='major'" :finger="scaleFinger" />
      <MinorScale v-if="scaleType=='minor'" :finger="scaleFinger" />
    </transition>
  </div>
</template>

<script>
import MajorScale from './MajorScale';
import MinorScale from './MinorScale';

import Delimiter from '~/components/utilities/Delimiter';

export default {
  data() {
    return {
      scaleType: 'major',
      scaleFinger: false
    }
  },
  components: {
    MajorScale,
    MinorScale,
    Delimiter
  }
}
</script>

<style>
.switch-field {
  margin-bottom: 10px;
  overflow: hidden;
}

.switch-field input {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  border: 0;
  overflow: hidden;
}

.switch-field label {
  background-color: #e4e4e4;
  color: rgba(0, 0, 0, 0.6);
  font-size: 14px;
  line-height: 1;
  text-align: center;
  padding: 8px 16px;
  margin-right: -1px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  transition: all 0.1s ease-in-out;
}

.switch-field label:hover {
  cursor: pointer;
}

.switch-field input:checked + label {
  background-color: #979494;
  box-shadow: none;
}

.switch-field label:first-of-type {
  border-radius: 4px 0 0 4px;
}

.switch-field label:last-of-type {
  border-radius: 0 4px 4px 0;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
/**checkbox */
.form-group {
  display: block;
  margin-bottom: 15px;
}

.form-group input {
  padding: 0;
  height: initial;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;
}

.form-group label {
  position: relative;
  cursor: pointer;
}

.form-group label:before {
  content: "";
  -webkit-appearance: none;
  background-color: transparent;
  border: 2px solid #0079bf;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05),
    inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
  padding: 10px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  margin-right: 5px;
}

.form-group input:checked + label:after {
  content: "";
  display: block;
  position: absolute;
  top: 2px;
  left: 9px;
  width: 6px;
  height: 14px;
  border: solid #0079bf;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
</style>